
.list_item {
  width: 271px;
  float: left;
  margin-right: 10px;
  margin-top: 1px;
  background-color: #fff;
  /*cursor:pointer;*/
}
.list_item:hover {
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
}
.list_item .list_top {
  position: relative;
}
.list_item .list_top p {
  width: 100%;
  font-size: 18px;
  color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: center;
}
.list_item .list_top .list_cover {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 32px;
  background-color: rgba(0, 0, 0, 0.3);
}
.list_item .list_foot {
  width: 100%;
  overflow: hidden;
  /*padding-left: 10px;*/
  text-align: left;
}
.list_item .list_foot div {
  margin-top: 10px;
  margin-bottom: 10px;
}
.list_item .list_foot span {
  width: 16px;
  height: 16px;
  display: inline-block;
  margin-right: 14px;
  margin-left: 10px;
}
.list_item .list_foot a {
  font-size: 14px;
}
.list_item .list_foot .userName span {
  background-image: url(img/sprite.png);
  background-position: 0 -20px;
}
.list_item .list_foot .phone span {
  background-image: url(img/sprite.png);
  background-position: -206px -52px;
}
.list_item .list_foot .position span {
  background-image: url(img/sprite.png);
  background-position: -206px -84px;
}



