
.warp {
  background-color: #f2f2f2;
}
.warp .personal {
  width: 1181px;
  padding: 20px 0;
  margin: 0 auto;
  text-align: left;
  overflow: hidden;
}
.warp .personal .leftbar {
  width: 174px;
  background-color: white;
  float: left;
  border: 1px solid #D3D3D3;
  margin-right: 20px;
}
.warp .personal .leftbar h2 {
  font-size: 18px;
  font-weight: 500;
}
.warp .personal .leftbar h2 em {
  float: right;
}
.warp .personal .leftbar h2 span {
  width: 18px;
  height: 18px;
  margin-right: 18px ;
  display: inline-block;
  background-image: url(img/sprite.png);
  background-position: 0 -20px;
  vertical-align: middle;
  /*解决加背景图的span不居中问题*/
}
.warp .personal .leftbar .left_title {
  padding: 20px ;
  border-bottom: 1px solid #d3d3d3;
}
.warp .personal .leftbar .left_cont {
  padding: 20px ;
}
.warp .personal .leftbar .left_cont li {
  padding: 10px 0;
}
.warp .personal .leftbar .left_cont li a:hover {
  color: #ec6a17;
  text-decoration: underline;
}



